 import stockfoot from "../../components/stockfoot.vue"
   //  ECharts 
 let echarts = require('echarts/lib/echarts');
// 
require('echarts/lib/chart/pie');
// 
require('echarts/lib/chart/bar');
require("echarts/lib/chart/line");
// 
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
require('echarts/lib/component/legend');
export default {
  name: "saocode",
components:{stockfoot},
  data() {
    return {
      parentMsg: this.$route.query.name,
      pcId:this.$route.query.pcid,
      planId:this.$route.query.planId
    };
  },
  methods: {
    stockchart(Data, dispose) {
      let myChart = echarts.init(document.getElementById("Chart"));
      let option = {
            tooltip : {
        trigger: 'item',
        formatter: "{a} <br/>{b} : {c} ({d}%)"
    },
    legend: {
        orient: 'vertical',
        left: 'left',
        data:  ['','','','']
    },
    series : [
        {
            name: '',
            type: 'pie',
            radius : '55%',
            center: ['50%', '60%'],
            data:Data.data,
            label: {
              normal: {
                // position: "inner",
                textStyle: {
                  fontWeight: "normal",
                  fontSize: 16
                }
              }
            },
             label: {
                normal: {
                    formatter: '{b|{b}}{c}',
                    borderColor: '#aaa',
                    borderRadius: 4,
                    rich: {
                        a: {
                            color: '#999',
                            lineHeight: 22,
                            align: 'center'
                        },
                        hr: {
                            borderColor: '#aaa',
                            width: '100%',
                            borderWidth: 0.5,
                            height: 0
                        },
                        b: {
                            fontSize: 16,
                            lineHeight: 33
                        },
                        per: {
                            color: '#eee',
                            backgroundColor: '#334455',
                            padding: [2, 4],
                            borderRadius: 2
                        }
                    }
                }
            },
        }
    ]
      }
      // 
      myChart.setOption(option);
    },
  },
  mounted() {
      let Data = [];
      this.$http
        .get("/API/stockservice/GetStockTj",{params:{"pcId":this.pcId}}, {emulateJSON: true })
        .then(res => {
          Data = JSON.parse(res.data);
          this.stockchart(Data);
        });
  }
};



